.btn,.btn a{text-decoration:none !important}.btn-rounded{border-radius:14px}.btn.btn-darkgrey{background:#444;border-color:#444;color:#fff}.btn.btn-darkgrey:focus,.btn.btn-darkgrey:hover{background:#222;border-color:#222}.btn.btn-springgreen{background:#00ff7f;border-color:#00ff7f;color:#fff}.btn.btn-springgreen:focus,.btn.btn-springgreen:hover{background:mediumspringgreen;border-color:mediumspringgreen}.btn.btn-mediumturquoise{background:mediumturquoise;border-color:mediumturquoise;color:#fff}.btn.btn-mediumturquoise:focus,.btn.btn-mediumturquoise:hover{background:darkturquoise;border-color:darkturquoise}.btn.btn-transparent{background:none;border-color:#fff;color:#fff}.btn.btn-transparent:hover{background:#fff;color:#444}
